*{
  font-family: Arial, Helvetica, sans-serif;
}


body{
  padding: 60px;
}

input{
  color: gray;
  width: 100%;
  font-size: 1.2rem;
}

.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card{
    align-items: center;
    border:1px solid gray;
    border-radius: 6px;
    margin-bottom: 10px;
    margin-right: 10px;
    overflow: hidden;
}

.card img{
  max-height: 270px;
}